@import "const";

.alert {
  display: none;
  position: fixed;
  height: 100%;
  top: 0;
  left: 0;
  width: 100%;
  align-items: center;
  justify-content: center;
  background-color: rgba(0,0,0, 0.5);
  z-index: 20;

  &__wrap {
    max-width: 512px;
    min-height: 192px;
    background-color: #ffffff;
    margin: 0 10px;

    h2 {
      margin-top: 0;
      margin-bottom: 15px;
      font: 600 20px/1.4 'Open Sans', sans-serif;
      color: #000000;
    }

    p {
      margin-top: 0;
      margin-bottom: 0;
      font: 16px/1.5 'Open Sans', sans-serif;
      color: #000000;
    }
  }

  &__btn {
    border: none;
    padding-left: 55px;
    padding-right: 55px;
  }

  &__success {
    display: none;

    .form-success & {
      display: block;
    }
  }

  &__error {
    display: none;

    .form-error & {
      display: block;
    }
  }

  &__header {
    display: flex;
    align-items: flex-start;
    padding: 25px 25px 10px;
    border-bottom: 1px solid #e6e6e6;

    div {
      padding-left: 25px;
    }
  }

  &__footer {
    padding: 10px;
    display: flex;
    justify-content: flex-end;
  }

  .form-success & {
    display: flex;
  }
}
